.swiper{
    &-pagination{
        display: flex;
        align-items: center;
        &-bullet{
            background: var(--Nobel);
            opacity: 1;
            width: 7px;
            height: 7px;
            z-index: 1;
            &-active{
                background: transparent;
                border: 1px solid var(--Wedgewood);
                width: 9px;
                height: 9px;
            }
        }
    }
    &-button{
        &-prev, &-next{
            width: 18px;
            height: 36px;
            color: #ababab;
            transition: all .3s ease-out;
            z-index: 1;
            &::after{
                font-size: 36px;
                display: block;
                content: "\E001";
                font-family: "icons";
            }
            &:hover{
                color: var(--Charcoal);
                transition: all .3s ease-out;
            }
        }
        &-prev{
            &::after{
                transform: rotate(180deg);
            }
        }
    }
}